  <el-menu
  id="nav"
  :default-active="activeNav"
  class="no-border-right"
  :class="{'vertical-nav': showType == 'vertical'}"
  :mode="showType"
  background-color="#fff"
  unique-opened
  text-color="#606266"
  @select="navClicked"
  active-text-color="#409EFF">
    <el-submenu index="1" v-if="checkAuthority('LessonManageIndex')">
      <template slot="title"><i class="fa fa-book"></i>&nbsp;</template>
      <el-menu-item v-if="checkSecondAuthority('LessonSystem')" index="LessonSystem"></el-menu-item> 
      <el-menu-item v-if="checkSecondAuthority('LessonList')" index="LessonList"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('TestList')" index="TestList"></el-menu-item> 
      <el-menu-item v-if="checkSecondAuthority('CourseCount')" index="CourseCount"></el-menu-item>
      <!-- <el-menu-item index="1-6"></el-menu-item> -->
    </el-submenu>
    <el-submenu index="3" v-if="checkAuthority('LabManageIndex')">
      <template slot="title"><i class="fa fa-cubes"></i>&nbsp;</template>
      <el-menu-item v-if="checkSecondAuthority('LabList')" index="LabList"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('ClassroomList')" index="ClassroomList"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('WorkList')" index="WorkList"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('ClassOccupy')" index="ClassOccupy"></el-menu-item> 
    </el-submenu>
    <el-submenu index="4" v-if="checkAuthority('DeviceManageIndex')">
      <template slot="title"><i class="fa fa-flask"></i>&nbsp;</template>
      <el-menu-item v-if="checkSecondAuthority('Standard')" index="Standard"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('DeviceList')" index="DeviceList"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Rent')" index="Rent"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Repair')" index="Repair"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Scrap')" index="Scrap"></el-menu-item>
    </el-submenu>
    <el-submenu index="9" v-if="checkAuthority('ConsumablesManageIndex')">
      <template slot="title"><i class="fa fa-gavel"></i>&nbsp;</template>
      <el-menu-item v-if="checkSecondAuthority('Consume')" index="Consume"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Warehouse')" index="Warehouse"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Record')" index="Record"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Out')" index="Out"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('MyOut')" index="MyOut"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('MyDevice')" index="MyDevice"></el-menu-item>
    </el-submenu>
    <el-submenu index="5" v-if="checkAuthority('PersonManageIndex')">
      <template slot="title"><i class="fa fa-group"></i>&nbsp;</template>
      <el-menu-item v-if="checkSecondAuthority('RoleList')" index="RoleList"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Authority')" index="Authority"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('UserList')" index="UserList"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Faculty')" index="Faculty"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Empower')" index="Empower"></el-menu-item>
    </el-submenu>
    <el-submenu index="6" v-if="checkAuthority('EptManageIndex')">
      <template slot="title"><i class="fa fa-tasks"></i>&nbsp;</template> 
      <el-menu-item v-if="checkSecondAuthority('ReportScore')" index="ReportScore"></el-menu-item> 
      <el-menu-item v-if="checkSecondAuthority('ExerciseScore')" index="ExerciseScore"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('ReportTaskList')" index="ReportTaskList"></el-menu-item> 
      <el-menu-item v-if="checkSecondAuthority('ExperimentalExercises')" index="ExperimentalExercises"></el-menu-item> 
      <el-menu-item v-if="checkSecondAuthority('ExperimentalQuestionnaire')" index="ExperimentalQuestionnaire"></el-menu-item> 
    </el-submenu>
    <el-submenu index="7" v-if="checkAuthority('LogManageIndex')">
      <template slot="title"><i class="fa fa-align-left"></i>&nbsp;</template>
      <el-menu-item v-if="checkSecondAuthority('User')" index="User"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('Security')" index="Security"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('System')" index="System"></el-menu-item>
    </el-submenu>
    <el-submenu index="8" v-if="checkAuthority('SettingsIndex')">
      <template slot="title"><i class="fa fa-gear"></i>&nbsp;</template>
      <el-menu-item v-if="checkSecondAuthority('HomeSetting')" index="HomeSetting"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('ArticleSetting')" index="ArticleSetting"></el-menu-item>
      <el-menu-item v-if="checkSecondAuthority('SchoolYear')" index="SchoolYear"></el-menu-item>
    </el-submenu>
  </el-menu>
</template>

<script>
  import lunar from "../plugins/lunar.js";
  export default {
    data () {
      return {
        navArray:              [],
        activeNav:             this.$route.name || '',
        pageAuthority:         null,
        pageAuthorityCodeList: [],
      }
    },
    props: {
      showType: { // type
        required: true,
        type: String,
        default: "vertical"
      },
      resource: { // 
        required: true,
        default: "vertical"
      }
    },
    watch: {
    },
    created () {
      this.setPageAuthority()
    },
    mounted () {
    },
    methods: {
      /**
       * 
       * @return {[type]} [description]
       */
      setPageAuthority() {
        let self = this
        try{
          JSON.parse(sessionStorage.getItem("Authority")).forEach((item, index) => {
            if(item.code == "PageAuthority"){
              self.pageAuthority = item.mate
            }
          })
        } catch (error) {
          self.pageAuthority = null
        }
        //code ()
        this.pageAuthorityCodeList = this.pageAuthority.map(item => {
          return item.code
        })
        // //  (forEach )
        // for(var i = 0; i < this.pageAuthorityCodeList.length; i++) {
        //   if(!this.pageAuthorityCodeList[i]) {
        //     this.pageAuthorityCodeList.splice(i--, 1);
        //   }
        // }
        // console.log(this.pageAuthorityCodeList)
      },
      checkAuthority (code) {
        let flag = false
        // holdOntrue 
        this.pageAuthority.forEach((item, index) => {
          if(code == item.code && item.holdOn) {
            flag = true
          }
        })
        //   true
        if(this.pageAuthorityCodeList.join(",").indexOf(code) == -1) {
          flag = true
        }
        return flag
      },
      /**
       * 
       * @param  {[type]} code [description]
       * @return {[type]}      [description]
       */
      checkSecondAuthority(code) {
        let Authority = null
        let secondAuthority = false
        try{
          JSON.parse(sessionStorage.getItem("Authority")).forEach((item, index) => {
            if(item.code == code) {
              secondAuthority = item.holdOn
            }
          })
        } catch (error) {
          secondAuthority = false
        }
        return secondAuthority
      },
      navClicked (path, pathArray) {
        let target = this
        if(path.indexOf("-") == -1) {
          this.$router.push({name: path})
          this.activeNav = path
        } else {
          this.$message.warning("", 100)
        }
      },
    }
  }
</script>
