/************   
 props
 uploadSuccess  response
*********/
<template>
  <div>  
    <el-upload 
      class="custom-avatar"
      :disabled="isDisabled"
      ref="customUpload"
      :action="basePath"
      :headers="headers"
      :file-list="fileList"
      :show-file-list="false"
      :before-upload="beforeUpload"
      :on-success="uploadSuccess"
      :data="data">
      <div class="" v-loading="uploadLoading">
        <img v-if="resetAvatar" :src="common.filePath + resetAvatar" class="avatar" :class="size"/>
        <i v-else class="el-icon-plus avatar-uploader-icon" :class="size"></i>
      </div>
    </el-upload>
  </div>
</template>

<script>
  export default {
    watch: {
      file: {
        handler(newV, oldV) {
          console.log(newV)
        },
        deep: true
      },
      avatar: {
        handler(newV, oldV) {
          this.resetAvatar=newV
          console.log(newV)
        },
        deep: true
      }
    },
    data () {
      return {
        headers: {},
        uploadLoading: false,
        typeList: { // type
          jpg:  "image/jpeg",
          gif:  "image/gif",
          png:  "image/png",
        },
        basePath: this.common.basePath + this.url,
        fileList: [],
        resetAvatar:this.avatar
      }
    },
    props: {
      url: { //URL 
        type: String,
        default: "/file/uplode"
      },
      fileTypes: { // 
        type: Array,
        default: function () {
          return []
        }
      },
      showSuccessNotice: { // 
        default: false
      },
      isDisabled: {
        type: Boolean,
        default: false, //false
      },
      size: { // 
        type: String,
        default: 'mini',
      },
      data: { // 
        required: false,
        type: Object,
        default: () => {}
      },
      avatar: {
        type: String,
        default:null
      }
    },
    mounted () {
    },
    methods: {
      /**
       * headers
       * @param  {[type]} file [description]
       * @return {[type]}      [description]
       */
      beforeUpload(file) {
        console.log(file.type)
        let flag = false //false  true

        if(this.fileTypes.length == 0) { // 
          flag = true
        } else {
          if(!file.type) { //  type = ""
            flag = false
          } else { //   
            this.fileTypes.forEach((item, index) => { //
              if(file.type == this.typeList[item]) {
                flag = true
              }
            })
          }
        }
        
        if (!flag) {
          this.$message.error(`${this.fileTypes.join(', ')}`)
          return false
        }
        try {
          if(sessionStorage.getItem("Access-Token")) {
            this.headers['Access-Token'] = JSON.parse(sessionStorage.getItem("Access-Token") || null);
          }
          if(sessionStorage.getItem("Access-Key")) {
            this.headers['Access-Key'] = JSON.parse(sessionStorage.getItem("Access-Key") || null);
          }
        } catch(e) {
          this.$message.error("Access-Token  Access-Key , !")
          return false
        }
        this.headers['nonce']      = Math.floor(Math.random()*10000000000000000) // 0-10000000000000000
        this.headers['Time-Stamp'] = (new Date()).valueOf() // 
        this.headers['secret']     = "labmiss" // 
        this.headers['type']       = "PC"      // 

        let object = {
          'nonce':      this.headers.nonce,
          'Time-Stamp': this.headers['Time-Stamp'],
          'secret':     this.headers.secret,
          'Access-Key': this.headers['Access-Key'],
        }

        let array = []
        for ( let k in object ) {
          array.push(k)
        }

        array.sort()
        let FinalArray = [] //
        array.forEach( (item, index) => {
          FinalArray.push(object[item])
        })
        this.headers['sign'] = window.md5Js(FinalArray.join(""))// sign
        this.uploadLoading = true
      },
      uploadSuccess(response, file, fileList) { 
        if(response.meta.code == 0) {
          // let newfileList = fileList.map((item, index) => {
            //console.log(item.response)
            // return {
            //   name: item.name,
            //   url: item.response.data.path
            // }
          // })
          let result = {}
          if(response.data) {
            result = {
              name: file.name,
              path: file.response.data.path
            }
          }
          
          this.$emit("uploadSuccess", result)
          this.resetAvatar = result.path;
          this.showSuccessNotice ? this.$message.success(`${file.name} !`) : null
          this.fileList = []
          this.uploadLoading = false
        } else {
          this.$message.error(response.meta.message)
          this.fileList = []
          this.uploadLoading = false
        }
      }
    }
  }
</script>

<style lang="less">
  input.upload-file-modify {
    position: absolute;
    height: 35px;
    width: 90px;
    opacity: 0;
  }
  .custom-avatar{
    .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      color: #8c939d;
      text-align: center;
    }
    .avatar-uploader-icon.mini {
      font-size: 28px;
      width: 108px;
      height: 108px;
      line-height: 108px;
    }
    .avatar-uploader-icon.small {
      font-size: 28px;
      width: 128px;
      height: 128px;
      line-height: 128px;
    }
    .avatar-uploader-icon.medium {
      font-size: 28px;
      width: 158px;
      height: 158px;
      line-height: 158px;
    }
    .avatar-uploader-icon.large {
      font-size: 28px;
      width: 178px;
      height: 178px;
      line-height: 178px;
    }
    .avatar {
      display: block;
    }
    .avatar.mini {
      width: 108px;
      height: 108px;
    }
    .avatar.small {
      width: 128px;
      height: 128px;
    }
    .avatar.medium {
      width: 158px;
      height: 158px;
    }
    .avatar.large {
      width: 178px;
      height: 178px;
    }
  }
</style>
