import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    noticeNum: null, //

    dialogQRcodeVisible: false,
    QRcodeValue: "...",
    typeTree: [],
    authority: null //, 
  },
  mutations: {
    /**
     * vuex
     * @param {[type]} state [description]
     * @param {[type]} data  [description]
     */
    setAuthority(state, data) {
      state.authority = data
    },
    exchangeShowStatus(state, data) {// 
      if(data) {
        state.dialogQRcodeVisible = true
        state.QRcodeValue = data.qr_code
      } else {
        state.dialogQRcodeVisible = false
      }
    },
    exchangeShowStatus_(state, data) {// 
      if(data) {
        state.dialogQRcodeVisible = true
        state.QRcodeValue = data.qrCode
      } else {
        state.dialogQRcodeVisible = false
      }
    },
    /**
     * 
     * @param  {[type]} state [description]
     * @param  {number} data   
     * @return {[type]}       [description]
     */
    change (state, data) {
      state.noticeNum = data
    },
    /**
     * 
     * @param {[type]} state [description]
     * @param {[type]} data  [description]
     */
    setTypeTree(state, data) {
      state.typeTree = data
    },
  },
  actions: {

  }
})
