  /**
   * 
   * @param  {[type]} row [description]
   * @param  {[type]} url [description]
   * @return {[type]}     [description]
   */
  returnDevice(row, url) {
    let p = new Promise((resolve, reject) => {
      Vue.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      })
      .then(() => {
        let config = {
          url: `${this.basePath}${url}`,
          method: "patch",
          data: qs.stringify({
            id: [row.id].toString(0),
            de_id: [row.de_id].toString(0),
            user_id: parseInt(sessionStorage.getItem("Access-Key"))
          })
        }
        Vue.axios(config).then(response => {
          Vue.$message.success("");
          resolve("")
        }).catch(error => {
          Vue.$message.error(error);
          reject("")
        })
      })
      .catch(() => {
      });
    })
    return p
  },
  /**
   * 
   * @return {[type]} [description]
   */
  scrapDevice(row, type) {
    let p = new Promise((resolve, reject) => {
      Vue.$prompt('<span><i class="required-label"></i></span>', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        dangerouslyUseHTMLString: true,
        inputValidator: (value) => {
          if(value) {
            return true
          } else {
            Vue.$message.error("")
            return false
          }
        },
        inputErrorMessage: ""
      }).then(({value}) => {
        let config = {
          method: "post",
          url: `${this.basePath}/scrap/add`,
          data: { 
            reason: value,
            de_id: [row.de_id],
            user_id: JSON.parse(sessionStorage.getItem("Access-Key")) || null,
            type: type
          }
        }
        if(value) {
          Vue.axios(config).then((response) => {
            Vue.$message.success(response.data.meta.message)
            resolve("")
          }).catch((errorMsg) => {
            Vue.$message.error(errorMsg)
            reject("")
          })
        } else {
          reject("")
        }
      }).catch((error) => {
      });
    })
    return p   
  },
