/************   
 props
 uploadSuccess  response
*********/
<template>
  <div>  
    <el-upload 
      :disabled="isDisabled"
      ref="customUpload"
      :action="basePath"
      :headers="headers"
      :file-list="fileList"
      :show-file-list="false"
      :before-upload="beforeUpload"
      :on-success="uploadSuccess"
      :data="data"
    ><el-button :disabled="isDisabled" type="primary" :size="size" :loading="uploadLoading">{{ uploadLoading ? "..." :  buttonText }}</el-button></el-upload>
  </div>
</template>

<script>
  export default {
    watch: {
      file: {
        handler(newV, oldV) {
          console.log(newV)
        },
        deep: true
      }
    },
    data () {
      return {
        headers: {},
        uploadLoading: false,
        typeList: { // type
          xls:  "application/vnd.ms-excel",
          xlsx: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
          doc:  "application/msword",
          docx: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
          ppt:  "application/vnd.ms-powerpoint",
          pptx: "application/vnd.openxmlformats-officedocument.presentationml.presentation",
          pdf:  "application/pdf",
          jpg:  "image/jpeg",
          gif:  "image/gif",
          png:  "image/png",
          mp4:  "video/mp4",
          avi:  "video/avi",
          mpeg: "video/mpeg",
        },
        basePath: this.common.basePath + this.url,
        fileList: []
      }
    },
    props: {
      url: { //URL 
        type: String,
        default: "/file/uplode"
      },
      fileTypes: { // 
        type: Array,
        default: function () {
          return []
        }
      },
      showSuccessNotice: { // 
        default: false
      },
      buttonText: { // 
        type: String,
        default: ""
      },
      isDisabled: {
        type: Boolean,
        default: false, //false
      },
      size: { // 
        type: String,
        default: 'mini',
      },
      data: { // 
        required: false,
        type: Object,
        default: () => {}
      }
    },
    mounted () {
    },
    methods: {
      /**
       * headers
       * @param  {[type]} file [description]
       * @return {[type]}      [description]
       */
      beforeUpload(file) {
        console.log(file.type)
        let flag = false //false  true

        if(this.fileTypes.length == 0) { // 
          flag = true
        } else {
          if(!file.type) { //  type = ""
            flag = false
          } else { //   
            this.fileTypes.forEach((item, index) => { //
              if(file.type == this.typeList[item]) {
                flag = true
              }
            })
          }
        }
        
        if (!flag) {
          this.$message.error(`${this.fileTypes.join(', ')}`)
          return false
        }
        try {
          if(sessionStorage.getItem("Access-Token")) {
            this.headers['Access-Token'] = JSON.parse(sessionStorage.getItem("Access-Token") || null);
          }
          if(sessionStorage.getItem("Access-Key")) {
            this.headers['Access-Key'] = JSON.parse(sessionStorage.getItem("Access-Key") || null);
          }
        } catch(e) {
          this.$message.error("Access-Token  Access-Key , !")
          return false
        }
        this.headers['nonce']      = Math.floor(Math.random()*10000000000000000) // 0-10000000000000000
        this.headers['Time-Stamp'] = (new Date()).valueOf() // 
        this.headers['secret']     = "labmiss" // 
        this.headers['type']       = "PC"      // 

        let object = {
          'nonce':      this.headers.nonce,
          'Time-Stamp': this.headers['Time-Stamp'],
          'secret':     this.headers.secret,
          'Access-Key': this.headers['Access-Key'],
        }

        let array = []
        for ( let k in object ) {
          array.push(k)
        }

        array.sort()
        let FinalArray = [] //
        array.forEach( (item, index) => {
          FinalArray.push(object[item])
        })
        this.headers['sign'] = window.md5Js(FinalArray.join(""))// sign
        this.uploadLoading = true
      },
      uploadSuccess(response, file, fileList) { 
        if(response.meta.code == 0) {
          // let newfileList = fileList.map((item, index) => {
            //console.log(item.response)
            // return {
            //   name: item.name,
            //   url: item.response.data.path
            // }
          // })
          let result = {}
          if(response.data) {
            result = {
              name: file.name,
              path: file.response.data.path
            }
          }
          
          this.$emit("uploadSuccess", result)
          this.showSuccessNotice ? this.$message.success(`${file.name} !`) : null
          this.fileList = []
          this.uploadLoading = false
        } else {
          this.$message.error(response.meta.message)
          this.fileList = []
          this.uploadLoading = false
        }
      }
    }
  }
</script>

<style>
  input.upload-file-modify {
    position: absolute;
    height: 35px;
    width: 90px;
    opacity: 0;
  }
</style>
