<template>
  <div class="feedback">
    <div class="build-form">
      <el-row :gutter="20">
        <el-col :span="14">
          <div class="form-box">
            <el-form :model="buildForm" ref="buildForm" label-width="100px" class="demo-dynamic" size="middle">
              <el-form-item
                prop="name"
                label=""
                :rules="[{ required: true, message: '', trigger: 'blur' },]">
                <el-input v-model="buildForm.name"></el-input>
              </el-form-item>
              <el-form-item
                prop="enclosure"
                label=""
                :rules="[{ required: true, message: '', trigger: 'blur' },]">
                <el-upload
                  class="avatar-uploader"
                  :action="`${common.msBasePath}/file/uplode`"
                  :show-file-list="false"
                  :auto-upload="true"
                  multiple
                  :limit="9999"
                  :before-upload="beforeAvatarUpload"
                  :http-request="uploadFile">
                  <img v-if="buildForm.enclosure" :src="'https://static.crledu.com'+buildForm.enclosure" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item
                prop="desc"
                label=""
                :rules="[{ required: true, message: '', trigger: 'blur' },{ min: 1, max: 255, message: ' 1  255 ', trigger: 'blur' },]">
                <el-input type="textarea" v-model="buildForm.desc" rows="6"></el-input>
              </el-form-item>
              <el-form-item
                prop="tel"
                label=""
                :rules="[{ required: true, message: '', trigger: 'blur' },{ pattern: /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/, message: '' }]">
                <el-input v-model="buildForm.tel"></el-input>
              </el-form-item> 
            </el-form>
          </div>
        </el-col>
        <el-col :span="10">
          <div class="function-box">
            <el-form class="function-box-title"><el-form-item label="" class="is-required"></el-form-item></el-form>
            <el-tree
              v-loading="loading"
              :data="projectFunctionList"
              show-checkbox
              node-key="id"
              ref="tree"
              :default-expanded-keys="expandedKeysArr"
              highlight-current
              :props="defaultFunctionProps"
              @node-click="handleNodeClick"
              @check="handleNodeCheckedClick">
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span @click="" class="fl">{{ node.label }}</span>
                <!-- <el-button type="text" size="mini">{{ node.label }}</el-button> -->
                <i class="el-icon-arrow-right fr i-mart2" :ref="`arrow${data.id}`" v-if="data.childrens.length==0"></i>
                <i class="fr i-mart2" :ref="`arrow${data.id}`" v-if="data.childrens.length!=0"></i>
              </span>
            </el-tree>
          </div>
        </el-col>
      </el-row>
      <div class="build-btns">
        <el-button @click="displayDialog" size="small"> </el-button>
        <el-button @click="resetForm('buildForm')" size="small"> </el-button>
        <el-button type="primary" @click="submitForm('buildForm')" size="small"> </el-button>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        loading:false,
        buildForm: {//
          enclosure: null,
          desc: null,
          tel: null,
          name: null,
        },
        projectFunctionList:null,//
        defaultFunctionProps: {
          children: 'childrens',
          label: 'name'
        },
        addList:null,//
        expandedKeysArr:[],//
      }
    },
    props:{
      dialogVisible: Boolean,  //
    },
    methods: {
      /**/
      displayDialog() {
        this.$emit('changeDialogVisible',false);//displayDialogchangeDialogVisible
      },
      //-------------------------------------------------------------------------------------
      /* 
        
       */
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isPNG = file.type === 'image/png';

        if (!isJPG&&!isPNG) {
          this.$message.error(' JPG/PNG !');
        }
        return isJPG || isPNG;
      },  
      /* 
        
      */
      uploadFile(param){
        var fileObj = param.file;
        // FormData 
        var form = new FormData();
        // 
        form.append("multipartFile", fileObj);
        this.axios.post(`${this.common.msBasePath}/other/upload`,form,{ headers: { 'Content-Type': 'multipart/form-data' } }).then((response) => {
          this.$message.success("", 20);
          this.buildForm.enclosure = response.data.data;
        }).catch((error) => {
          this.$message.error(error);
        });
      },

      //-------------------------------------------------------------------------------------
      //id
      getCheckedKeys() {
        this.function = this.$refs.tree.getCheckedKeys()
      },
      //
      resetChecked() {
        this.$refs.tree.setCheckedKeys([]);
      },
      /*
        
      */
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.getCheckedKeys();//id
            if(this.function.length!=0){
              let form = {
                title:this.buildForm.name,
                enclosure:this.buildForm.enclosure,
                detailed:this.buildForm.desc,
                phone:this.buildForm.tel,
                function:this.function.join(","),
                project_id:this.activeIndex?this.activeIndex:null,
                deploy_id:1,
                /*"backcontent": "string",
                "checktime": "2019-01-10T09:18:15.648Z",
                "content": "string",*/
              }
              this.axios.post(`${this.common.msBasePath}/other/insert`,form).then( (response) => {
                if(response.data.meta.code === 0){
                  this.$message.success('', 20);
                  this.$emit('changeDialogVisible',false);//displayDialogchangeDialogVisible 
                }else{
                  this.$message.error(response.data.meta.message, 20);   
                }
              }).catch( (error) => {
                this.$message.error(error);
              });
            }else{
              this.$message.error("");
            }
          } else {return false;}
        });
      },
      /*
        
      */
     
      resetForm(formName) {
        this.getFunctionList();//()
        this.resetChecked();//
        this.$refs[formName].resetFields();//
      },

      //-------------------------------------------------------------------------------------
      /* 
        GET  
      */
      getFunctionList(){
        this.loading = true;
        this.axios.get(`${this.common.msBasePath}/other/show`,{params:{id:null,deploy_id:6,}}).then( (response) => {
          if(response.data.meta.code === 0){
            this.projectFunctionList = response.data.data?response.data.data:null;//
          }else{
            this.$message.error(response.data.meta.message, 20);   
          }
          this.loading = false;
        }).catch( (error) => {
          this.$message.error(error);
          this.loading = false;
        });
      },
      handleNodeClick(data,node,VueComponent) {
        if(data.childrens.length===0){
          this.loading = true;
          this.axios.get(`${this.common.msBasePath}/other/show`,{params:{id:data.id,deploy_id:6,}}).then( (response) => {//GET  id
            if(response.data.meta.code === 0){
              let addList = response.data.data?response.data.data:null;//
              if(addList.length===0){
                this.$message.info('...', 20);
                let el = 'arrow'+data.id;
                this.$refs[el].style.display = 'none';
              }else{

                this.expandedKeysArr.push(data.id);
                if(this.expandedKeysArr.length>1){
                  this.expandedKeysArr = this.expandedKeysArr.slice(this.expandedKeysArr.length-1)
                }

                if (!data.childrens) {
                  this.$set(data, 'childrens', []);
                }
                for(var i = 0;i<addList.length;i++){
                  data.childrens.push(addList[i]);
                }
              }
            }else{
              this.$message.error(response.data.meta.message, 20);   
            }
            setTimeout(() => {
              this.loading = false;
            }, 100)
          }).catch( (error) => {
            this.$message.error(error);
            this.loading = false;
          });
        }else{
          // this.$message.info('...', 20); 
        }
      },
      handleNodeCheckedClick(data,val){
        if( val.checkedKeys.indexOf(data.id) > -1){//
          if(data.childrens.length===0){
            this.loading = true;
            this.axios.get(`${this.common.msBasePath}/other/show`,{params:{id:data.id,deploy_id:6,}}).then( (response) => {//GET  id
              if(response.data.meta.code === 0){
                let addList = response.data.data?response.data.data:null;//
                if(addList.length===0){
                  this.$message.info('...', 20);
                  let el = 'arrow'+data.id;
                  this.$refs[el].style.display = 'none';
                }else{

                  this.expandedKeysArr.push(data.id);
                  if(this.expandedKeysArr.length>1){
                    this.expandedKeysArr = this.expandedKeysArr.slice(this.expandedKeysArr.length-1)
                  }

                  if (!data.childrens) {
                    this.$set(data, 'childrens', []);
                  }
                  for(var i = 0;i<addList.length;i++){
                    data.childrens.push(addList[i]);
                  }
                }
              }else{
                this.$message.error(response.data.meta.message, 20);   
              }
              setTimeout(() => {
                 this.loading = false;
              }, 100)
            }).catch( (error) => {
              this.$message.error(error);
              this.loading = false;
            });
          }else{
            // this.$message.info('...', 20);
          }
        }
      },
    },
    mounted(){
      //
      if(this.$refs['buildForm']){
        this.$refs['buildForm'].resetFields();
      }
      //()
      this.getFunctionList();
      console.log(this.dialogVisible)
    },
  }
</script>
<style lang="less">
  .function-box .el-tree{
    max-height: 400px;padding: 10px 5px;overflow:auto;
  }
  /*style*/
  .custom-tree-node{
    display:block;width:100%;
    .i-mart2{
      margin-top: 2px;
    }
  }
  /*  */
  .build-form{
    padding: 20px;
    box-sizing: border-box;
    .form-box{
      border-right: 1px solid #ddd;
      padding-right: 20px;
      .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
      }
      .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
      }
      .avatar-uploader-icon{
        font-size: 28px;
        color: #8c939d;
        width: 150px;
        height: 150px;
        line-height: 150px;
        text-align: center;
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        background: white;
      }
      .avatar-uploader-icon:hover{
        border-color: #409EFF;
      }
      .avatar {
        width: 150px;
        height: 150px;
        display: block;
      }
    }
    .function-box{
      box-sizing: border-box;
      .function-box-title{
        .el-form-item{
          margin-bottom: 0;
        }
      }
    }
    .build-btns{
      margin: 10px auto 0;
      text-align: right;
      border-top: 1px solid #ddd;
      padding-top: 10px;
    }
  }
</style>